@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
.Header{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #0f273d;
  width: 100%;
  height: 80px;
}
.Header-logo{
  position : absolute;
  top : 30px;
  left : 20px;
  width: 90px;
}

@media (max-width : 700px){
  .Header-logo{
    display: none;
  }
}
.Search{
  position: relative;
  width: 35%;
  height: 35px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 700px) {
  .Search {
    width: 100%;
  }
}
.Search-icon{
  z-index: 1;
  position: absolute;
  top: 9px;
  left: 28px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 18px;
  height: 18px;
}
.Search-input {
  box-sizing: border-box;
  background-color: #1f364d;
  border-radius: 4px;
  border: 0;
  padding-left: 35px;
  color: white;
  opacity: .8;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
  width: 100%;
  height: 35px;
}

.Search-input:focus {
  outline: none;
  opacity: 1;
}

.Search ::-webkit-input-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search ::-moz-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search :-ms-input-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search ::-ms-input-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search ::placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search-loading {
  position: absolute;
  top: 9px;
  right: 28px;
}

.Search-result-container {
  position: relative;
  width: 100%;
  max-height: 299px;
  overflow-y: auto;
  background-color: #0f273d;
  border: 1px solid #0c2033;
  border-radius: 4px;
  box-shadow: 0px 0px 40px 0px#1f364d;
  margin-top: 10px;
}

.Search-result {
  color: #9cb3c9;
  padding: 15px 0 15px 35px;
  border-bottom: 2px solid #0c2033;
  cursor: pointer;
}

.Search-result:hover {
  color: #fff;
}

.Search-no-result {
  color: #9cb3c9;
  padding: 15px 0 15px 35px;
  border-bottom: 1px solid #0f273d;
}

.Loading{ 
  width: 28px;
  height: 28px;
  display: inline-block;
  border: 2px solid #fff;
  border-right-color: transparent;
  border-radius: 50%;
  -webkit-animation: rotate 1s infinite linear;
          animation: rotate 1s infinite linear;
}

@-webkit-keyframes rotate {
   0% {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg)
   }
   
   50%{
     -webkit-transform: rotate(180deg);
             transform: rotate(180deg)
   }

   100%{
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg)
   }
}

@keyframes rotate {
   0% {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg)
   }
   
   50%{
     -webkit-transform: rotate(180deg);
             transform: rotate(180deg)
   }

   100%{
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg)
   }
}
.Table-container{
  overflow-x: auto;
}
.Table{
  width: 100%;
  border-collapse:collapse;
  border-spacing: 0;
}
.Table-head{
  background-color: #0c2033;
}
.Table-head tr th {
  padding: 10px 20px;
  color: #9cb3c9;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
}
.Table-body{
  text-align: left;
  background-color: #0f273d;
}
.Table-body tr td{
  padding: 24px 20px;
  border-bottom: 2px solid #0c2033;
  color: #fff;
  cursor: pointer;
}
.Table-rank{
  color: #9cb3c9;
  margin-right: 18px;
  font-size: 12px;
}
.Table-dollar{
  color: #9cb3c9;
  margin-right: 6px;
}
.Pagination{
  margin: 50px auto;
  text-align: center;
}
.Pagination-button{
  text-align: center;
  border: none;
  border-radius: 16px;
  background-color: #4997e5;
  -webkit-transition: background-color 2s;
  transition: background-color 2s;
  color: white;
  cursor: pointer;
  margin: 10px;
  width: 44px;
  height: 34px; 
}
.Pagination-button:hover{
  background-color: #457cb2;
}
.Pagination-button:focus{
  outline: none;
}
.Pagination-button:disabled{
  background-color: #1f364d;
  cursor: not-allowed;
}
.Pagination-info{
  font-size: 12px;
}
