body {
  background-color: #0c2033;
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  letter-spacing: .5px;
  font-size: 16px;
  font-weight: 300;
}

.percent-raised {
  color: #3cd483;
}

.percent-fallen {
  color: #d64d96;
}

.loading-container {
  width: 100%;
  text-align: center;
  margin: 40px auto;
}

.error {
  width: 100%;
  margin: 40px 0;
  text-align: center;
  color: #d64d96;
}

/* Remove close icon from input on IE */
input::-ms-clear {
  display: none;
}

.NotFound{
  width: 100%;
  text-align: center;
  margin-top: 60px;
}
.NotFound-title{
  font-weight: 400;
  color: #9cb3c9;
}
.NotFound-link{
  display: inline-block;
  margin-top: 40px;
  color: #fff;
  text-decoration: none;
  border: 1px solid #9cb3c9;
  border-radius: 4px;
  padding: 18px;
  -webkit-transition: border .2s;
  transition: border .2s;
}

.NotFound-link:hover {
  border: 1px solid #fff;
}
.Detail{
  display : flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 40px;
  padding: 0 60px;
}

.Detail-heading{
  font-size: 24px;
  font-weight: 300;
}
.Detail-container{
  width: 100%;
  max-width: 400px;
  margin-top: 30px;
  padding: 40px 40px 0;
  border-radius: 4px;
  box-shadow: 0px 0px 40px 0px #1f364d;
}

.Detail-item{
  margin-bottom: 50px;
}

.Detail-value{
  border-radius: 20px;
  background-color: #1f364d;
  font-size: 14px;
  padding: 8px 12px;
  margin-left: 10px;
}

.Detail-title{
  display: block;
  color: #9cb3c9;
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 10px;
}

.Detail-dollar {
  color: #9cb3c9;
  margin-right: 6px;
}

